body {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 30px;
  color: white;
  background: brown;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
}

input[type="submit"]:disabled {
  cursor: not-allowed !important;
  opacity: 0.6 !important;
}
